body {
  margin: auto;
  width: 800px; }

.calendar {
  margin-top: 100px;
  box-sizing: border-box;
  display: flex;
  flex: 1 0 700px;
  flex-wrap: wrap; }
  .calendar div {
    transition: transform 0.5s, background-color 0.5s;
    background: #eee;
    border: solid 1px black;
    box-sizing: border-box;
    margin: 0 0 1px 1px;
    width: 100px;
    height: 100px;
    font-family: Arial, sans-serif;
    text-align: center;
    font-size: 30px;
    line-height: 100px; }
  .calendar div:hover {
    transform: translate(0, -30px);
    background-color: pink;
    cursor: pointer; }

